@import url('https://fonts.googleapis.com/css?family=Questrial');
body {
  margin: 0;
}
body #app {
  display: block;
  font-family: 'Questrial', Verdana, sans-serif;
  color: #222;
}
body #app .selectors {
  width: 100%;
  margin: auto;
  position: fixed;
  bottom: 0;
  background: inherit;
  z-index: 100;
}
body #app .selectors .selector {
  display: inline-block;
  width: 33.33333333%;
  margin: auto;
  background: #222;
  color: #fff;
  top: 20px;
  text-align: center;
  padding: 20px 0;
}
body #app .selectors .selector:hover {
  cursor: pointer;
  background: #7F7E7E;
}
body #app .collection_container {
  width: 100%;
  max-width: 600px;
  z-index: 50;
  margin: auto;
  position: relative;
  padding: 20px 0;
}
@media screen and (min-width: 600px) {
  body #app .collection_container {
    padding: 20px;
  }
}
body #app .collection_container .collection_header {
  width: 100%;
  text-align: center;
  margin: auto auto 20px auto;
  font-size: 30px;
}
body #app .collection_container .input_container {
  width: 95%;
  max-width: 600px;
  position: fixed;
  margin: auto;
  margin-left: 6px;
  padding: 10px 0;
  background: #fff;
  bottom: 55px;
}
@media screen and (min-width: 600px) {
  body #app .collection_container .input_container {
    margin-left: 0;
  }
}
body #app .collection_container .input_container .search_field {
  padding: 10px 0;
  margin: auto;
  width: 100%;
}
body #app .collection_container .album_wrapper {
  padding-top: 20px;
  font-size: 14px;
  line-height: 1.75;
  width: 95%;
  margin: auto;
}
@media screen and (min-width: 600px) {
  body #app .collection_container .album_wrapper {
    font-size: 18px;
    width: 100%;
  }
}
body #app .collection_container .album_wrapper .description_text {
  font-weight: 700;
  margin: auto auto 10px auto;
}
body #app .collection_container .album_wrapper .album_element {
  margin: auto auto 15px auto;
}
